import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import './scss/GridTheme.scss'
import { getColumnCount, getSizeInPercent } from "../../../utils";
class GridTheme extends PureComponent {

  componentDidMount(){
    this.props.loadBranding()
  }
  
  render() {
    
    const { postData, completeDataObject, CardStyle, ThemeStyle, clickToShowPopUp, onClickPopUpSlider, containerSize } = this.props;
    const minCardSize = ( CardStyle.minimumWidth || 150 ) + 20
    const columns =  getColumnCount(containerSize.width, ThemeStyle.column, minCardSize);
    const postSize = getSizeInPercent(containerSize.width, columns)

    return (
      <Masonry
        className={`tb_g_post_container`}
        tabIndex='0'
        aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
        elementType={'div'}
        options={{
          transitionDuration: 0,
          percentPosition: true,
          itemSelector: '.tb_g_post_wrapper',
          horizontalOrder: true,
          resize: true,
        }}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
        imagesLoadedOptions={{}}
        enableResizableChildren={true}>{
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item];
            return  <Card clickToShowPopUp={clickToShowPopUp} key={index} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} postSize={postSize} itemIndex={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} />
          })
      }
      </Masonry>
    );
  }
}

export default GridTheme