import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import { isInstaUser } from "../../../../utils";
const AuthorDetails = ({ Post, CardStyle, inView }) => {

  const { author, networkId } = Post
  const instaUser =  isInstaUser(author.name);
  const postAuthor = (CardStyle.author.status  && !instaUser) ? true : false;
  return <div className="tb_g_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_g_author">
      {postAuthor ? <Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_g_author_profile'} inView={inView} /> : ''}
      <div className="tb_g_author_info">
        {postAuthor ? <div className="tb_g_authorname">{author.name}</div> : ''}
        {CardStyle.timeStatus ? 
        <Time createdAt={Post.createdAt}  timeClass={`tb_g_time tb-cTBfont-${CardStyle.author.variant}`} /> : ''}
      </div>
    </div>
  </div>
}
export default AuthorDetails